import regionPicker from "@/components/region-picker/region-picker.vue";
import {
	getAddress,
	addAddress
} from '@/http/address.js';
import {formatAddress} from '@/static/utils.js';
export default {
	components: {
		regionPicker
	},
	data() {
		return {
			regions: ['请选择', '请选择', '请选择'],
			username:'',
			phone:'',
			detailedAddress:'',
			id:0
		}
	},
	onLoad(option) {
		
		this.userId = uni.getStorageSync('userId');
		if (Number(option.id) > 0) {
			this.getAddressInfo({
				id: option.id,
				userId: this.userId
			})
		}
	},
	methods: {
		region_change(e) {
			this.regions = e.detail.value;
		},
		async getAddressInfo(option) {
			let result = await getAddress(option);
			console.log(result.data)
			if(result.data !== null){
				let {consignee,cellphone,address,id} = result.data;
				console.log(id)
				// 使用格式化函数格式化地址
				const addressAry = formatAddress(address);
				this.username = consignee;
				this.phone = cellphone;
				this.id = id;
				this.detailedAddress = addressAry[1];
				this.regions = addressAry[0];
			}

		},
		submitAddress(){
			let {regions,username,phone,detailedAddress,id,userId} = this;
			console.log(regions,username,phone,detailedAddress);
			if(regions === ''){
				uni.showToast({
					title:'选择市省区'
				})
				return 
			}
			if(username === ''){
				uni.showToast({
					title:'请输入姓名'
				})
				return 
			}
			if(phone === ''){
				uni.showToast({
					title:'请输入手机号'
				})
				return 
			}
			if(detailedAddress === ''){
				uni.showToast({
					title:'填写地址'
				})
				return 
			}
			let address = regions.join("") + detailedAddress;
			addAddress({
				userId:userId,
				id,
				consignee:username,
				cellphone:phone,
				address:address
			}).then(res=>{
				console.log(res)
				if(res.data !== null && res.data.status === 0){
					uni.showToast({
						title:'添加成功'
					})
				}
			})
		}
	}
}
